.STIncomplete{
    text-align: center;
    background: rgb(236, 236, 236);
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    padding: 10px;
}
.STIncomplete > p{
    margin-bottom: 20px;
    color: black
}
.STIncomplete :nth-child(2){
    margin-right: 20px;
}
.STIncomplete button{
    height: fit-content;
    width: 100px;
    position: relative;
    padding: 0;
    border: none;
    background: transparent;
    cursor: pointer;
    outline-offset: 4px;
    transition: filter 250ms;
    user-select: none;
    touch-action: manipulation;
}