.STTask{
    width: 300px;
    min-height: 175px;
    border-radius: 5px;
    background: rgba(30,30,30);
    margin:0 auto 20px auto;
    padding: 15px;
    display: grid;
    grid-template-columns: 83% 17%;
    grid-template-rows: 20% 80%;
    place-items: center;
}
.STTask-title{
    grid-column: 1 / 3;
    text-align: center;
    width: 100%;
}
.STTask-title *{
    text-align: center;
    font-size: 1.9rem;
    max-width: 300px;
    overflow: hidden;
    color:white;
}
.STTask-text{
    width: 100%;
    padding-right: 10px;
    min-height: 100px;
    height: 100%;
}
.STTask-text p{
    overflow: hidden;
    font-weight:400;
    font-size: 1.6rem;
    user-select:text;
    width: 100%;
    color:white;
}

.STargetTask-options{
    background-color: aliceblue;
    min-height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5px;
    width: 45px;
    border-radius: 14px;
    align-self: baseline;
}

.STargetTask--options-container{
    position: relative;
    height: 76px;
    overflow: hidden;
}
.STargetTask--options{
    transition: top 1s;
    top: -85%;
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-between;
}
.STargetTask--options.open{
    transition: top 1s;
    top: 14%;
}
.STargetIcon-config{
    transition: transform 1.4s;
    transform: rotate(-30deg);
}
.STargetIcon-config.open{
    transition: transform 1.4s;
    transform: rotate(60deg);
}
