.STCount{
    text-align: center;
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
}
.STCount *{
    font-weight: bolder;
    font-size: 2.5rem;
}
.STCount p{
    color: white;
}
.STCount span{
    color: rgb(255,20,20);
}
.STCount-loading-text{
    color: white;
    font-weight: bolder;
    font-size: 2.5rem;
    text-align: center;
}
.STCount-loading-text::after{
    content: ".";
    font-size: 4rem;
    animation-duration: 2s;
    animation-name: cargando;
    animation-iteration-count: infinite;
    animation-direction:normal;
}
@keyframes  cargando{
    from{
        content: " .";
    }
    70%{
        content: " ..";
    }
    to{
        content: " ...";
    }
  }