.CAlert-sync{
    position: fixed;
    padding-top: 7px;
    bottom: 50px;
    width: 200px;
    top: calc(100% - 100px);
    left: 50px;
    place-items: center;
    justify-content: center;
    z-index: 1;
    background-color: rgba(220, 219, 219, 0.8);
    display: flex;
    border-radius: 5px;
    border: white 1px solid;
}
.CAlert--text{
    color: black;
    font-size: 1.6rem;
}
/* From uiverse.io by @gagan-gv */
.loader {
    border: 0 solid transparent;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    align-self:flex-start ;
   }
   
   .loader::before,
   .loader::after {
    content: '';
    border: 7px solid black;
    border-radius: 50%;
    width: inherit;
    height: inherit;
    position: absolute;
    animation: loader 2s linear infinite;
    opacity: 0;
   }
   
   .loader::before {
    animation-delay: 1s;
   }
   
   @keyframes loader {
    0% {
     transform: scale(1);
     opacity: 0;
    }
   
    50% {
     opacity: 1;
    }
   
    100% {
     transform: scale(0);
     opacity: 0;
    }
   }